
*,
body {
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
}

.title {
  font-size: 2.5rem;
  text-align: center;
  margin: 0 0 30px 0;
  padding: 35px 0;
  color: #61dafb;
  background-color: #282c34;
}
.btn {
  font-size: 1.2rem;
  font-weight: 500;
  padding: 10px 30px;
  color: rgba(0, 0, 0, 0.87);
  -webkit-box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
          box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
  background-color: #e0e0e0;
  margin: 0 auto 29px auto;
  display: block;
  border: 0;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  cursor: pointer;
}
.btn:hover {
  -webkit-box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
          box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
  background-color: #d5d5d5;
}
.user-list-header {
  margin: 0 auto;
  max-width: 400px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #282c34;
  background-color: #61dafb;
}
.user-list-header span {
  display: block;
  padding: 5px 15px;
  font-size: 1.25rem;
}
.user-list-header span:first-child {
  width: 150px;
  border-right: 1px solid #282c34;
}
.user-list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  max-width: 400px;
  background-color: #282c34;
  color: #fff
}
.user-list-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0;
  border-bottom: 1px solid #fff;
}
.user-list span {
  display: block;
  padding: 10px 15px;
  overflow: hidden;
}
.user-list span:first-child {
  width: 150px;
  border-right: 1px solid #fff;
}
.user-list span:nth-child(2) {
  width: 250px;
  min-width: 250px;
}
.user-list-inner {
  list-style: none;
  background-color: #61dafb;
}
.user-list-inner .user-list-bar {
  background-color: #282c34;
}

.overlay {
  z-index: 5;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  top: 0;
  left: 0;
  display: none;
}
.modal {
  position: fixed;
  left: calc(50% - 200px);
  top: calc(50% - 150px);
  z-index: 6;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  border-radius: 10px;
  margin: 0 auto;
  background-color: #fff;
  display: none;
}
.modal-title {
  font-size: 1.35rem;
}
.modal-body {
  padding: 30px 0 10px 0;
}
.modal-close {
  position: absolute;
  top: 16px;
  right: 21px;
  font-size: 2rem;
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.modal-list {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}
.modal-list li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 15px;
}
.modal-list span {
  min-width: 130px;
  font-size: 1rem;
}
.modal-list input {
  font-size: 1rem;
  border: 1px solid #282c34;
  padding: 5px;
  width: 100%;
}
.modal-list input:focus {
  outline: none;
}
.modal-list select {
  font-size: 1rem;
  border: 1px solid #282c34;
  padding: 5px;
  width: 100%;
}
.modal-list select:focus {
  outline: none;
}
.modal-btn {
  margin: 0 auto;
}
.active {
  display: block;
}
